import React from 'react';
import 'tachyons';
import './Navigation.css'
import Resume from './Resume.pdf';
const Navigation = ({ClickImagebar,ClickAboutMe, ClickAchievements , ClickProjects}) => {
    return(
        <div className='bar'>
            <a href={Resume} ><button className='but special f5  shadow-5 link pointer dim grow dib br3 ma2' >My Resume</button></a>
            <button onClick={ClickProjects} className='but f5  shadow-5 link pointer dim grow dib br3 ma2'>My Projects</button>
            <button onClick={ClickAchievements} className='but f5  shadow-5 link pointer dim grow dib br3 ma2'>My Achievements</button>
            <button className='but f5  shadow-5 link pointer dim grow dib br3 ma2' onClick={ClickAboutMe}>About Me</button>
            <button className='but f5  shadow-5 link pointer dim grow dib br3 ma2' onClick={ClickImagebar}>Home</button>
        </div>
    );
}

export default Navigation;