import './App.css';
import React,{Component} from 'react';
import Navigation from './Navigation';
import Imagebar from './Imagebar';
import Particles from 'react-particles-js';
//import Scroll from './Scroll';
import AboutMe from './AboutMe';
import Projects from './Projects';
import Achievements from './Achievements';
const particlesOptions = {
  particles:{
    number: {
      value: 80,
      density: {
        enable: true,
        value_area: 800
      }
    }
  }
}

class App extends Component{
  constructor(){
    super()
    this.state={
      
    }
    this.imagebar = React.createRef();
    this.aboutme = React.createRef();
    this.achievements = React.createRef();
    this.projects = React.createRef();
  }
  ClickImagebar = (event) =>{
    if(this.imagebar.current){
      this.imagebar.current.scrollIntoView({ 
         behavior: "smooth", 
         block: "center"
      })
  }
  }
  ClickAboutMe = (event) =>{
    if(this.aboutme.current){
      this.aboutme.current.scrollIntoView({ 
         behavior: "smooth", 
         block: "center"
      })
  }
  }
  ClickAchievements = (event) =>{
    if(this.achievements.current){
      this.achievements.current.scrollIntoView({ 
         behavior: "smooth", 
         block: "center"
      })
  }
  }
  ClickProjects = (event) =>{
    if(this.projects.current){
      this.projects.current.scrollIntoView({ 
         behavior: "smooth", 
         block: "center"
      })
  }
  }
  render(){
    return (
      <div className='back'>
        <Particles className='particles' params={particlesOptions} />
       <Navigation ClickImagebar={this.ClickImagebar} ClickAboutMe={this.ClickAboutMe} ClickAchievements={this.ClickAchievements} ClickProjects={this.ClickProjects}/>
       {/*<Scroll>*/}
          <div ref={this.imagebar}>
            <Imagebar />
          </div>
          
          <div  ref={this.aboutme}>
            <AboutMe />
          </div>
          <div ref={this.achievements}>
            <Achievements />
          </div>
          <div ref={this.projects}>
            <Projects />
          </div>
          
          <div className='br2' style={{border:'solid white'}}></div>
          <p className='tc white'><b>Pulkit Chaudhary, @ 2020</b></p>
       {/*</Scroll>*/}
      </div>
    );
  }
}

export default App;

