import React from 'react';
import 'tachyons';
import Tilt from 'react-tilt';
import './Imagebar.css';
import Myphoto from './Myphoto.jpg'
import Typing from 'react-typing-animation';
const Imagebar = () => {
    return(
        <div className='card tc pa1 shadow-10'>
            <Tilt className="Tilt br2 shadow-2 tc" options={{ max : 15 }} style={{ margin:'auto'}} >
                <div className="Tilt-inner tc">
                    
                </div>
            </Tilt>
            <img className='borrad shadow-5' style={{paddingTop: '5px'}}src={Myphoto}/>
            <div style={{height:120}}>
            <Typing>
            <div>
            <Typing.Delay ms={2000} />
                <h1 className='info' style={{color:'red'}}><b>Pulkit Chaudhary</b></h1>
            
            {/*<Typing.Backspace count={2000}/>*/}
                
                {/*<h2 className='info'>Have a Good Day..!!</h2>*/}
            </div>
            </Typing>
            <Typing>
                <div>
                <Typing.Delay ms={3000} />
                <h2 className='info'>Welcome to my corner of the Web.</h2>
                <Typing.Delay ms={2000} />
                <Typing.Backspace count={40}/>
                </div>
            </Typing>
            <Typing>
                <div>
                <Typing.Delay ms={14000} />
                <h2 className='info'>3rd year B.TECH U.G.</h2>
                <Typing.Delay ms={1000} />
                <h2 className='info'>BIT Mesra</h2>
                </div>
            </Typing>
            </div>
            <div className='tc'>
                <a href="https://www.facebook.com/pulkit.chaudhary.7374/" style={{color:'cornflowerblue'}} className="fa fa-facebook  pa4 shadow social"></a>
                <a href="https://www.instagram.com/pulkit4399/?hl=en" style={{color:'pink'}} className="fa fa-instagram  pa4 shadow social"></a>
                <a href="http://www.gmail.com" style={{color:'firebrick'}} className="fa fa-google  pa4 shadow social"></a>
                <a href="https://www.linkedin.com/in/pulkit-chaudhary-34664b191/" style={{color:'deepskyblue'}} className="fa fa-linkedin  pa4 shadow social" aria-hidden="true"></a>
            </div>
                   {/*<h4 className='info'>3<sup>rd</sup> year U.G., B.TECH</h4>
                    <h4 className='info'>COMPUTER SCIENCE AND ENGINEERING</h4>
    <h3 className='info'>Birla Institute Of Technology, Mesra</h3>*/}
            
        </div>
    );
    
}

export default Imagebar;